@tailwind base;
@tailwind components;
@tailwind utilities;
body {
  font-family: 'Noto Sans TC', 'sans-serif';
  font-size: 16px; }

[data-hasOrder="true"] {
  @apply flex; }

[data-hasOrder="false"] {
  @apply hidden; }

[data-popUp="true"] {
  @apply flex; }

[data-popUp="false"] {
  @apply hidden; }
